import React, { Component } from 'react';
import logo from './Icon_blue.png';
import './App.css';
// import header from './site-header.jpg';
import PrivacyPage from './modules/privacy';
import HomePage from './modules/home'
import ContactPage from './modules/contact'
import { BrowserRouter, Link, Route } from 'react-router-dom';
import Button from '@material-ui/core/Button'


class App extends Component {
  render() {
    return (
      <BrowserRouter>
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Helpiyo</h1>
        </div>
        <div className={"linkBar"}>
          {/*<Button component={Link} to={"/home"} color={"primary"}>*/}
            {/*Home*/}
          {/*</Button>*/}
          <Button href={"https://play.google.com/store/apps/details?id=me.helpiyo.app"} color={"primary"} target={"_blank"}>
            Go to App
          </Button>
          <Button component={Link} to={"/privacy"} color={"primary"}>
            Privacy Policy
          </Button>
          <Button component={Link} to={"/contact"} color={"primary"}>
            Contact Us
          </Button>
        </div>
        <Route exact path={"/home"} component={HomePage}/>
        <Route exact path={"/privacy"} component={PrivacyPage}/>
        <Route exact path={"/contact"} component={ContactPage}/>

      </div>
      </BrowserRouter>
    );
  }
}

export default App;



